<template>
  <div id="admin-home">
    <el-page-header @back="goBack" content="">
    </el-page-header>
    <div class="header">
      <h1 style="color: white"></h1>
    </div>
    <div class="content">
      <el-tabs v-model="tabActive">
        <el-tab-pane label="" name="first">
          <div class="item-reg">
            <el-table :data="userList" style="width: 100%">
              <el-table-column prop="lastLoginTime" label="" width></el-table-column>
              <el-table-column prop="userName" label="" width="180"></el-table-column>
              <el-table-column prop="userEmail" label="" width="180"></el-table-column>
              <el-table-column prop="userNo" label="" width=""></el-table-column>
              <el-table-column prop="isBan" label="" width="180">
                <div slot-scope="scope">
                  <div v-if="scope.row.isBan === 0"></div>
                  <div v-else></div>
                </div>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="danger" @click="banUser(scope.row.userNo)" v-show="scope.row.isBan===0">
                  </el-button>
                  <el-button type="success" @click="unBanUser(scope.row.userNo)" v-show="scope.row.isBan===1">
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <!--          -->
        <el-tab-pane label="" name="second">
          <div class="item-audit">
            <el-table :data="commList" style="width: 100%" height="90vh" stripe
                      :default-sort="{prop: 'commodity.commName', order: 'increasing'}">
              <el-table-column label="" width="120">
                <template slot-scope="scope">
                  <el-image :src="scope.row.commPicList[0]" fit='cover' :preview-src-list="scope.row.commPicList"
                            style="width: 70px;height: 70px"></el-image>
                </template>
              </el-table-column>
              <el-table-column prop="commName" label="" sortable></el-table-column>
              <el-table-column prop="commDesc" label=""></el-table-column>
              <el-table-column prop="createUser" label="" width="180" sortable></el-table-column>
              <el-table-column prop="auditStatus" label="" sortable
                               :filters="[{text: '', value: 0},{text: '', value: 1},{text: '', value: 2}]"
                               :filter-method="filterHandler">
                <div slot-scope="scope">
                  <div v-if="scope.row.auditStatus === 0"></div>
                  <div v-else-if="scope.row.auditStatus === 1"></div>
                  <div v-else></div>
                </div>
              </el-table-column>
              <el-table-column label="" width="300">
                <template slot-scope="scope">
                  <el-button type="primary" @click="checkCommo(scope.row.commNo)" size="small">
                  </el-button>
                  <el-button type="primary" @click="allowCommo(scope.row.commNo)" size="small"
                             v-show="scope.row.auditStatus===0||scope.row.auditStatus===2">
                  </el-button>
                  <el-button type="danger" @click="refuseCommo(scope.row.commNo)" size="small"
                             v-show="scope.row.auditStatus===0||scope.row.auditStatus===1">
                  </el-button>
                  <el-button type="danger" @click="deleteCommo(scope.row.commNo)" size="small">
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <!--        -->
        <el-tab-pane label="" name="third">
          <div class="item-reg">
            <el-table :data="userList" style="width: 100%">
              <el-table-column prop="userName" label="" width="180"></el-table-column>
              <el-table-column prop="photoUrl" label=""></el-table-column>
              <el-table-column prop="userRealName" label="" width="180"></el-table-column>
              <el-table-column prop="college" label="" width=""></el-table-column>
              <el-table-column prop="sno" label="" width=""></el-table-column>
              <el-table-column prop="authentication" label="" width="">
                <div slot-scope="scope">
                  <div v-if="scope.row.auditStatus === 0"></div>
                  <div v-else-if="scope.row.auditStatus === 1"></div>
                  <div v-else-if="scope.row.auditStatus === 2"></div>
                  <div v-else></div>
                </div>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="danger" @click="changeAuthentication(scope.row.userNo,2)"
                             v-show="scope.row.isBan===0">
                    
                  </el-button>
                  <el-button type="success" @click="changeAuthentication(scope.row.userNo,3)"
                             v-show="scope.row.isBan===1">
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
export default {
  comments: {},
  data() {
    return {
      userName: '',
      tabActive: 'first',
      allUsers: [],
      auditCommos: [],
      userList: [],
      commList: [],
      token: this.$store.state.token,
      auditMsg: ''
    }
  },
  methods: {
    //
    allowCommo(commNo) {
      this.$axios.post('/shop/admin/auditComm', this.$qs.stringify({
            auditStatus: 1,
            auditor: this.userName,
            commNo: commNo
          }), {
            headers: {
              Authorization: this.token
            }
          }
      ).then(resp => {
        var data = resp.data
        if (data.code === 1) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
        }
        this.getCommList()
      }).catch(function (error) {
        console.log(error)
      })
    },
    //
    refuseCommo(commNo) {
      this.$prompt(':', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
        inputErrorMessage: '',
        inputPlaceholder: ''
      }).then(({value}) => {
        this.auditMsg = value
        console.log(this.auditMsg)
        this.$axios.post('/shop/admin/auditComm', this.$qs.stringify({
          auditMsg: this.auditMsg,
          auditStatus: 2,
          auditor: this.userName,
          commNo: commNo
        }), {
          headers: {
            Authorization: this.token
          }
        }).then(resp => {
          var data = resp.data
          if (data.code === 1) {
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
            this.auditMsg = ''
            this.getCommList()
          }
        }).catch(function (error) {
          console.log(error)
        })
      }).catch(() => {
        this.$notify({
          title: '',
          type: 'info',
          message: ''
        });
      });
    },
    // 
    deleteCommo(commNo) {
      this.$axios.post('/shop/commodity/deleteComm', this.$qs.stringify({
        commNo: commNo
      }), {
        headers: {
          Authorization: this.token
        }
      }).then(resp => {
        var data = resp.data
        if (data.code === 1) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
        } else {
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        }
      }).catch(function (error) {
        console.log(error)
      })
      this.getCommList()
    },
    banUser(userNo) {
      console.log(userNo)
      this.$axios.post('/shop/admin/setUserIsBan', this.$qs.stringify({
        isBan: 1,
        userNo: userNo
      }), {
        headers: {
          Authorization: this.token
        }
      }).then(resp => {
        var data = resp.data
        console.log(data)
        if (data.code === 1) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
        }
        this.getUserList()
      })
    },
    unBanUser(userNo) {
      console.log(userNo)
      this.$axios.post('/shop/admin/setUserIsBan', this.$qs.stringify({
        isBan: 0,
        userNo: userNo
      }), {
        headers: {
          Authorization: this.token
        }
      }).then(resp => {
        var data = resp.data
        console.log(data)
        if (data.code === 1) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
        }
      })
      this.getUserList()
    },
    changeAuthentication(userNo, authentication) {
      this.$axios.post('/shop/admin/setUserIsBan', this.$qs.stringify({
        authentication: authentication,
        userNo: userNo
      }), {
        headers: {
          Authorization: this.token
        }
      }).then(resp => {
        var data = resp.data
        console.log(data)
        if (data.code === 1) {
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
        }
      })
      this.getUserList()
    },
    //  
    initDate() {
      this.getUserList()
      this.getCommList()
    },
    //
    getUserList() {
      this.$axios.post('/shop/admin/userList', {}, {
        headers: {
          Authorization: this.token
        }
      }).then(resp => {
        var data = resp.data
        console.log(data)
        if (data.code === 1) {
          this.userList = data.obj
          for (let i = 0; i < this.userList.length; i++) {
            this.userList[i].lastLoginTime = this.rTime(this.userList[i].lastLoginTime);
          }
        }
      })
    },
    getCommList() {
      //  
      this.$axios.get('/shop/admin/commList', {
        headers: {
          Authorization: this.token
        }
      }).then(resp => {
        console.log(resp)
        var data = resp.data
        if (data.code === 1) {
          this.commList = data.obj
        }
      })
    },
    //  
    filterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },
    //  
    rTime: function (date) {
      var date1 = new Date(date).toJSON();
      return new Date(+new Date(date1) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    //  
    checkCommo(commNo) {

      this.$store.commit('GET_COMM', commNo)
      this.$router.push({path: '/check'})
    },
    //  
    goBack() {
      this.$router.push({path: '/user'})
    },
  },
  mounted() {
    this.token = this.$store.state.token
    console.log(this.token)
    if (this.token === '') {
      this.$message({
        message: '',
        type: 'error'
      });
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.$router.push({path: '/login'});
      }, 1500);
    }
    this.initDate()
  }
}
</script>
<style lang="scss" scoped>
#admin-home {
  position: relative;

  .header {
    height: 60px;
    background: rgb(70, 70, 70);
    margin: 0.5em auto;
  }

  .content {
    width: 90%;
    margin: 30px auto;
  }
}
</style>
